import React from 'react'
import Paginator from '../Common/Paginator';
import User from './User';

const Users = ({
                   users, totalUsersCount, pageSize, currentPage, onPageChange,
                   followingInProgress, follow, unFollow
               , ...props}) => {
    //alert(`Перерисовывается Users, currentPage = ${currentPage}`);
    return <div>
        <Paginator totalItemsCount={totalUsersCount} pageSize={pageSize}
                   currentPage={currentPage} onPageChange={onPageChange}/>
        {users.map(u => <User user = {u} key = {u.id} followingInProgress = {followingInProgress}
                                follow = {follow} unFollow = {unFollow} />)
        }
    </div>
}
export default Users;