import React from 'react'
import styles from './users.module.css';
import defaultUserPhoto from '../../Assets/images/defaultuserphoto.png';
import {NavLink} from 'react-router-dom';

const User = ({user, key, followingInProgress, follow, unFollow}) => {
    return <div key = {key}>
            <span>
                <div>
                    <NavLink to={'/profile/' + user.id}>
                        <img src=
                                 {user.photos.small != null ? user.photos.small
                                     : defaultUserPhoto} className={styles.userPhoto}/>
                    </NavLink>
                </div>
        <div>{user.followed ?
            <button disabled={followingInProgress.some(id => id === user.id)}
                    onClick={() => {
                        unFollow(user.id)
                    }}>Unfollow</button>
            :
            <button disabled={followingInProgress.some(id => id === user.id)}
                    onClick={() => {
                        follow(user.id)
                    }}>Follow</button>}</div>
        <span>
            <div>{user.name}</div><div>{user.status}</div>
            <div>{"user.location.country" + user.id}</div>
            <div>{"user.location.city"}</div>
        </span>
            </span>
    </div>
}
export default User;