export const required = value => {
    if (value) return undefined;
    return "Field is required";
}
//Конструктор валидатора на максимальное число символов в текстовом поле
export const maxLengthCreator = (limit) => (value) => {
    if (value && value.length > limit) return `Max length is ${limit} symbols`;
    return undefined;
}
//Конструктор валидатора на максимльное введенное число
export const maxNumCreator = (limit) => (value) => {
    debugger
    if (value && value > limit) return `Max number is ${limit}`;
    return undefined;
}