import * as axios from 'axios';
import photo from '../../Assets/images/ava.png'

const instance = axios.create({
        withCredentials: true,
        baseURL: 'https://social-network.samuraijs.com/api/1.0/',
        headers: {"API-KEY": "438f0413-fb9f-45d9-9756-2b432579f6b7"}
    }
);


export const userAPI = {
    getUsers(currentPage = 1, pageSize = 10) {
        return instance.get(`users?page=${currentPage}&count=${pageSize}`)
            .then((response) => (response.data));
    },
    followUser(id) {
        return instance.post(`follow/${id}`).then((response) => (response.data));
    },
    unFollowUser(id) {
        return instance.delete(`follow/${id}`).then((response) => (response.data));
    },

    getUserProfile(id) {
        console.warn('Obsolete method. Please use profileAPI.getUserProfile (id)');
        return profileAPI.getUserProfile(id);
    },
    setUserProfile(id) {
        console.warn('Obsolete method. Please use profileAPI.setUserProfile (id)');
        return profileAPI.setUserProfile(id);
    }
}
export const profileAPI = {
    getUserProfile(id) {
        return instance.get(`profile/${id}`).then((response) => (response.data));
    },
    setUserProfile(id) {
        return instance.get(`/profile/${id}`).then((response) => (response.data));
    },
    getUserStatus(id) {
        return instance.get(`/profile/status/${id}`).then((response) => (response.data));
    },
    updateStatus(status) {
        debugger
        //instance.put(`/profile/photo`,{image: '../../Assets/images/ava.png'}).then((response) => (response.data))
        return instance.put(`/profile/status`, {status: status}).then((response) => (response.data))
    }
}

export const authAPI = {
    me() {
        return instance.get(`auth/me`).then((response) => (response.data));
    },
    login(email, password, rememberMe = false) {
        return instance.post(`auth/login`, {
            email: email,
            password: password,
            rememberMe: rememberMe
        }).then((responce) => (responce.data));
    },
    logout() {
        return instance.delete(`auth/login`).then((responce) => (responce.data));
    }
}