import {profileAPI, userAPI} from "../Components/api/api";

const ADD_POST = 'ADD_POST';
const SET_USER_PROFILE = 'SET_USER_PROFILE';
const GET_USER_STATUS = 'GET_USER_STATUS';
const SET_STATUS = 'SET_STATUS';


let initialstate = {
    posts: [
        {id: 1, text: 'Hello, how are you?', Numlikes: 15},
        {id: 2, text: `It's my first post`, Numlikes: 20}],
    profile: null,
    status: ''
}
const profileReducer = (state = initialstate, action) => {
    switch (action.type) {
        case ADD_POST:
            let newPost = {id: 3, text: action.newPostText, Numlikes: 0};
            return {
                ...state,
                posts: [...state.posts, newPost],
                newPostText: ''
            };
        case SET_USER_PROFILE:
            return {
                ...state,
                profile: action.profile
            };
        case GET_USER_STATUS:
            return {
                ...state,
                status: action.status
            };
        case SET_STATUS:
            return {
                ...state,
                status: action.status
            };


        default:
            return state;
    }
}

export const addPostActionCreator = (newPostText) => ({type: ADD_POST, newPostText});
export const setUserProfile = (profile) => ({type: SET_USER_PROFILE, profile});
export const getStatus = (status) => ({type: GET_USER_STATUS, status});
export const setNewStatus = (status) => ({type: SET_STATUS, status});


export const setProfile = (userId) =>
    async (dispatch) => {
        let data = await userAPI.setUserProfile(userId);
        dispatch(setUserProfile(data));
    }
export const getUserStatus = (userID) =>
    async (dispatch) => {
        let data = await profileAPI.getUserStatus(userID);
                dispatch(getStatus(data));
}

export const updateStatus = (status) =>
    async (dispatch) => {
        let data = await profileAPI.updateStatus(status);
                data.resultCode === 0 &&
                dispatch(setNewStatus(status));
}
export default profileReducer;