import React from 'react';
import s from './ProfileInfo.module.css';
import Preloader from '../../Common/Preloader';
import HaveAJobImage from '../../../Assets/images/haveajob.jpg';
import InSearchingForAJobImage from '../../../Assets/images/insearchingjob.jpg';
import noAvatarImage from '../../../Assets/images/noavatar.jpg'
import ProfileStatusWithHooks from "./ProfileStatusWithHooks";

const ProfileInfo = ({profile, status, updateStatus}) => {
    if (!profile) return <Preloader/>
    return (
        <div>
            <div className={s.ProfileWrapper}>
                <div className={s.ProfileLeftContent}>
                    {profile.fullName}
                    <div className={s.bigAvatar}><img
                        src={profile.photos.large ? profile.photos.large : noAvatarImage}/></div>
                    {/*<div className={s.AboutMe}>{props.profile.aboutMe}</div>*/}
                    <ProfileStatusWithHooks status={status} updateStatus={updateStatus}/>
                </div>
                <div className={s.ProfileRightContent}>
                    <div className={s.JobStatus}>
                        <div><img src={(profile.lookingForAJob) ? HaveAJobImage : InSearchingForAJobImage}/></div>
                        <div>{profile.lookingForAJobDescription}</div>
                    </div>
                </div>
            </div>
        </div>);
}
export default ProfileInfo;