import React from 'react';
import s from './Header.module.css';
import {NavLink} from "react-router-dom";
const Header = (props) => {
return (
    <header className={s.header}>
        `   <img src='https://e7.pngegg.com/pngimages/92/187/png-clipart-logo-company-business-creative-company-logo-free-logo-design-template-company.png'/>
    <div className={s.loginBlock}>
        {props.profile != null && props.profile.photos.small != null ?
            <span className={s.avatar}><img src = {props.profile.photos.small}/></span> : ''}
        {props.isAuthorized ?
            <div>{props.login} <button onClick = {props.logout}>Log out</button></div> :
            <NavLink to={'/login'}>Login</NavLink>}
    </div>
    </header>);
}
export default Header;