import React from 'react';
import s from './Dialogs.module.css';
import DialogItem from './DialogItem/DialogItem';
import Message from './Message/Message';
import {Field, reduxForm} from 'redux-form';
import {Textarea} from "../Common/FormsControls/FormsControls";
import {maxLengthCreator, required} from "../../utils/validators/validators";

const maxLength50 = maxLengthCreator(50);
const AddMessage = props => {
    return <form onSubmit={props.handleSubmit}>
        <div>
            <button className={s.button}> Add message</button>
        </div>
        <div><Field placeholder='Enter your message' component={Textarea} name='dialogsAddMessage'
        validate = {[required, maxLength50]}/></div>
    </form>
}
const AddMessageReduxForm = reduxForm({
    form: 'addMessage'
})(AddMessage);


const Dialogs = (props) => {
    let onAddMessageClick = (formData) => {
        props.onAddMessageClick(formData.dialogsAddMessage)
    }
    return (
        <div className={s.dialogs}>
            <div className={s.dialogsItems}>
                {
                    props.dialogsState.dialogs
                        .map(d => <DialogItem name={d.name} id={d.id}/>)
                }
                <AddMessageReduxForm onSubmit={onAddMessageClick}/>
            </div>
            <div className={s.messages}>
                {
                    props.dialogsState.messages
                        .map(m => <Message text={m.text}/>)
                }
            </div>
        </div>
    );
}

export default Dialogs;