import React, {useState} from 'react'
import styles from './paginator.module.css';
import {reduxForm} from "redux-form";
import {createField, Input} from "./FormsControls/FormsControls";
import errorstyles from './../Common/FormsControls/FormsControls.module.css'
import {maxNumCreator} from "../../utils/validators/validators";


const Paginator = ({totalItemsCount, pageSize, currentPage = 1, onPageChange, portionSize = 10}, ...props) => {
    const onScrollingPages = (p) => {
        onPageChange((p - 1) * portionSize + 1);
        setPortionNumber(p);
    }
    const onSubmit = (formData) => {
        onPageChange(formData.pageNum);
        setPortionNumber(Math.ceil(formData.pageNum / portionSize), 0);
    }
    const maxNumInput = maxNumCreator(Math.ceil(totalItemsCount/pageSize));
    const PageNumForm = ({handleSubmit, error}) => {
        return (
            <form  className={styles.form} onSubmit={handleSubmit}>
                {createField('Введите номер страницы', 'pageNum',Input, [maxNumInput], {type: 'input'})}
                {error && <div className={errorstyles.formSummaryError}>{error} </div>}
                <div>
                    <button className={styles.button}>Перейти</button>
                </div>
            </form>
        );
    }
    const PageNumReduxForm = reduxForm({
        form: 'pageNum'
    })(PageNumForm);

    let pagesCount = Math.ceil(totalItemsCount / pageSize);
    let pages = [];
    const portionCount = Math.ceil(pagesCount / portionSize);
    let [portionNumber, setPortionNumber] = useState(Math.ceil(currentPage / portionSize, 0));
    let leftPortionNumber = (portionNumber - 1) * portionSize + 1;
    let rightPortionNumber = portionNumber * portionSize;
    //alert(`Перерисовывается Paginator, currentPage = ${currentPage}, left = ${leftPortionNumber}, right = ${rightPortionNumber}`);

    for (let i = 0; i < pagesCount; i++) pages[i] = i + 1

    return <div className={styles.paginator}>
        {portionNumber > 1 &&
        <button onClick={() => onScrollingPages(1)}>{'<<'}</button>}
        {portionNumber > 1 &&
        <button onClick={() => onScrollingPages(portionNumber - 1)}>{'<'}</button>}

        {pages
            .filter(p => p >= leftPortionNumber && p <= rightPortionNumber)
            .map((p) => <span className={+currentPage === +p ? styles.selectedPage : styles.pageNumber}
                              onClick={(e) => {
                                  onPageChange(p)
                              }}>{p}</span>
            )}

        {portionNumber < portionCount &&
        <button onClick={() => onScrollingPages(portionNumber + 1)}>{'>'}</button>}
        {portionNumber < portionCount &&
        <button onClick={() => onScrollingPages(portionCount)}>{'>>'}</button>}
        <PageNumReduxForm onSubmit={onSubmit}/>
    </div>
}




export default Paginator;