import {authAPI} from "../Components/api/api";
import {stopSubmit} from "redux-form";

const SET_USER_DATA = 'SET_USER_DATA';
const IS_AUTHORIZED = 'IS_AUTHORIZED';
const SET_AUTH_PROFILE = 'SET_AUTH_PROFILE'

let initialstate = {
    userId: null,
    email: null,
    login: null,
    isAuthorized: false,
    profile: null
};
const authReducer = (state = initialstate, action) => {
    switch (action.type) {
        case SET_USER_DATA:
            return {
                ...state,
                ...action.data
            };
        case IS_AUTHORIZED:
            return {
                ...state,
                isAuthorized: action.isAuthorized,
            };
        case SET_AUTH_PROFILE:
            return {
                ...state,
                profile: {...action.profile}
            }

        default:
            return state;
    }
}

export const setAuthUserData = (userId, email, login) => ({type: SET_USER_DATA, data: {userId, email, login}});
export const setAuthorized = (isAuthorized) => ({type: IS_AUTHORIZED, isAuthorized});
export const setAuthProfile = (profile) => ({type: SET_AUTH_PROFILE, profile});

export const getAuthUserData = () =>
    async (dispatch) => {
        let data = await authAPI.me();
        if (data.resultCode === 0) {
            dispatch(setAuthorized(true));
            dispatch(setAuthUserData(data.data.id, data.data.email, data.data.login))
        } else {
            dispatch(setAuthorized(false));
        }
    }

export const login = (email, password, rememberMe) =>
    async (dispatch) => {
        let data = await authAPI.login(email, password, rememberMe);
        if (data.resultCode === 0) {
            dispatch(getAuthUserData());
        } else {
            let message = data.messages.length > 0 ? data.messages[0] : "Some error";
            dispatch(stopSubmit("login", {_error: message}));
        }
    }

export const logout = () =>
    async (dispatch) => {
        let data = await authAPI.logout();
        if (data.resultCode === 0) {
            dispatch(setAuthorized(false));
            dispatch(setAuthUserData(null, null, null))
        }
    }

export default authReducer;