import React from 'react';
import s from './Post.module.css';

const Post = (props) => {
    return (
        <div className={s.item}>
            <img src = 'https://klike.net/uploads/posts/2018-06/1528369868_15.jpg' />
            {props.message}
            <div>
                <span>Like {props.Numlikes}</span>
            </div>
        </div>
)
};
export default Post;