import React from 'react';
import s from './MyPosts.module.css';
import Post from './Post/Post';
import {Field, reduxForm} from 'redux-form';
import {maxLengthCreator, required} from '../../../utils/validators/validators';
import {Textarea} from '../../Common/FormsControls/FormsControls';

const maxLenght10 = maxLengthCreator(10);
const AddPost = (props) => {
    return <form onSubmit={props.handleSubmit}>
        <div><Field placeholder='Type message' component={Textarea} name='profilePostText' validate = {[required, maxLenght10]} /></div>
        <div><button>Add post</button></div>
    </form>
}

const AddPostReduxForm = reduxForm({
    form: 'addPost'
})(AddPost);

const MyPosts = React.memo(props => {
    let onAddPost = (formData) => {
        props.onAddPost(formData.profilePostText);
    }
    return (
        <div className={s.postsBlock}>
            <h3>My posts</h3>
            <div>
                <AddPostReduxForm onSubmit={onAddPost}/>
            </div>
            <div className={s.posts}>
                {
                    props.posts.map(p => <Post message={p.text} Numlikes={p.Numlikes}/>)
                }
            </div>
        </div>
    )
});
export default MyPosts;