import React from 'react';
import styles from './FormsControls.module.css'
import {Field} from "redux-form";

const FormControl = ({input,meta: {error, touched}, FormType, ...props}) => {
    let hasError = error && touched;
    return <div className={styles.formControl + ' ' + (hasError ? styles.error : '')}>
        <div>
            <FormType {...input} {...props} />
        </div>
        <span>{hasError && error}</span>
    </div>
}


export const Textarea = (props) => {
    return <FormControl {...props} FormType='textarea'/>;
}

export const Input = (props) => {
    return <FormControl {...props} FormType='input'/>;
}

export const createField = (placeholder, name, component, validators, props = {}, text = '')=> {
    return (
    <div><Field placeholder={placeholder} name={name} component={component}
                validate={validators} {...props} />{text}</div>);
}
