import {userAPI} from '../Components/api/api';
import {updateObjectinArray} from "../utils/validators/objects-helpers";

const FOLLOW = 'FOLLOW';
const UNFOLLOW = 'UNFOLLOW';
const SET_USERS = 'SET_USERS';
const SET_CURRENT_PAGE = 'SET_CURRENT_PAGE';
const SET_TOTAL_USERS_COUNT = 'SET_TOTAL_USERS_COUNT';
const TOGGLE_IS_FETCHING = 'TOGGLE_IS_FETCHING';
const TOGGLE_IS_FOLLOWING = 'TOGGLE_IS_FOLLOWING';

let initialstate = {
    users: [],
    pageSize: 5,
    totalUsersCount: 0,
    currentPage: 1,
    isFetching: true,
    followingInProgress: []
};
const usersReducer = (state = initialstate, action) => {
    switch (action.type) {
        case FOLLOW:
            return {
                ...state,
                users: updateObjectinArray(state.users, action.userId,"id", {followed:true})
            };
        case UNFOLLOW:
            return {
                ...state,
                users: updateObjectinArray(state.users, action.userId,"id", {followed:false})
            };
        case SET_USERS:
            return {
                ...state,
                users: action.users
            };
        case SET_CURRENT_PAGE:
            return {
                ...state,
                currentPage: action.currentPage
            }
        case SET_TOTAL_USERS_COUNT:
            return {
                ...state,
                totalUsersCount: action.totalCount
            }
        case TOGGLE_IS_FETCHING:
            return {
                ...state,
                isFetching: action.isFetching
            }
        case TOGGLE_IS_FOLLOWING:
            return {
                ...state,
                followingInProgress: action.isFetching ?
                    [...state.followingInProgress, action.userId] :
                    state.followingInProgress.filter(u => u != action.userId)
            }
        default:
            return state;
    }
}

export const onFollowed = (userId) => ({type: FOLLOW, userId});
export const onUnFollowed = (userId) => ({type: UNFOLLOW, userId});
export const setUsers = (users) => ({type: SET_USERS, users});
export const setCurrentPage = (currentPage) => ({type: SET_CURRENT_PAGE, currentPage});
export const setTotalUsersCount = (totalCount) => ({type: SET_TOTAL_USERS_COUNT, totalCount});
export const setToggleIsFetching = (isFetching) => ({type: TOGGLE_IS_FETCHING, isFetching});
export const setToggleIsFollowing = (isFetching, userId) => ({type: TOGGLE_IS_FOLLOWING, isFetching, userId});

export const getUsers = (currentPage, pageSize) => {
    return async (dispatch) => {
        dispatch(setToggleIsFetching(true));
        let data = await userAPI.getUsers(currentPage, pageSize)
        dispatch(setToggleIsFetching(false));
        dispatch(setUsers(data.items));
        dispatch(setTotalUsersCount(data.totalCount));
    }
}
const ToggleFollow = async(dispatch, userId, apiMethod, actionCreator) => {
    dispatch(setToggleIsFollowing(true, userId));
    let data = await apiMethod(userId);
    if (data.resultCode === 0) dispatch(actionCreator(userId))
    dispatch(setToggleIsFollowing(false, userId));
}
export const follow = (userId) => {
    return async (dispatch) => {
        ToggleFollow(dispatch, userId, userAPI.followUser.bind(userAPI), onFollowed);
    }
}
export const unFollow = (userId) => {
    return async (dispatch) => {
        ToggleFollow(dispatch, userId, userAPI.unFollowUser.bind(userAPI), onUnFollowed);
    }
}

export default usersReducer;